.container {
    position: relative;
	box-sizing: border-box;
	width: 1376px;
	// max-width: 95%;
	max-width: calc(100% - 36px);
	margin: 0 auto;
    position: relative;
	@include max(lg) {
		//1024
		width: 1168px;
		max-width: calc(100% - 32px * 2);
	}

	@include max(md) {
		max-width: calc(100% - 32px * 2);
	}

	@include max(sm) {
		max-width: calc(100% - 16px * 2);
	}
    &--pr-none {
        @include max(sm) {
            max-width: calc(100% - 0px);
            padding-left: 16px;
        }
        .catalog__button {
            right: 16px;
        }
    }
}

.button {
    &__circle {
		width: 130px;
		height: 130px;
		position: absolute;
		right: -18px;
		top: 5px;
		opacity: 1;
		will-change: transform, opacity;
		transform-style: preserve-3d;
        z-index: 900;
        cursor: pointer;
        @include max(xxl) {
            width: vw(130);
            height: vw(130);
        }
        @include max(sm) {
            width: vw2(72);
            height: vw2(68);
            right: vw2(-10);
            top: vw2(12)
        }
        &-inner {
			width: 100%;
			height: 100%;
			position: relative;
			display: flex;
			align-items: center;
			justify-content: center;
			border-radius: 50%;
			will-change: opacity;
			transform-style: preserve-3d;
		}

		&-outside {

			width: 100%;
			animation: rotateCircle 15s infinite linear;
            @include max(xxl) {
                // height: vw(130);
                width: vw(130);
            }
            @include max(sm) {
                width: vw2(72);
                // height: vw2(68);
            }
		}

		&-inside {
            position: absolute;
            top: 49%;
            left: 50%;
			transform: translateX(-50%) translateY(-50%);
			width: 100%;
            @include max(xxl) {
                // height: vw(130);
                width: vw(130);
            }
            @include max(sm) {
                width: vw2(72);
                // height: vw2(68);
            }
            // @include max(xxl) {
            //     left: vw(68);
            //     top: vw(65);
            //     width: vw(18);
            // }
            // @include max(sm) {
            //     width: vw2(11);
            //     left: vw2(35.5);
            //     top: vw2(35.5);
            // }
		}
    }
}
.swiper-buttons {
    position: absolute;
    display: flex;
    height: 100px;
    width: 100%;
    top: calc(50% - 28px);
    overflow: visible;
    @media (max-width: 1450px) {
      position: relative;
      justify-content: space-between;
      align-items: flex-start;
      height: 40px;
      top: 0;
      margin-top: 45px;
      bottom: 0;
      left: 0;
      width: 100%;
      max-width: 120px;
      top: auto;
      bottom: auto;
      left: auto;
      right: auto;
      align-self: flex-start;
    }
    @include max(md) {
      max-width: 92px;
      margin-top: 45px;
    }
    .swiper-button-prev,.swiper-button-next {
        width: 28px;
        height: 56px;
        top: 0;
        &::after {
            font-size: 38px;
        }
        @media (max-width: 1450px) {
            width: 49px;
            height: 49px;
            border-radius: 50%;
            display: flex;
            justify-content: center;
            align-items: center;
            position: relative;
            left: auto;
            right: auto;
            top: auto;
            bottom: auto;
            background: #F5F5F5;
            &::after {
            font-size: 20px;
            }
    
        }
        @include max(md) {
            width: 33px;
            height: 33px;
            &::after {
            font-size: 15px;
            }
    
        }
  
  
    }
    .swiper-button-prev {
        &::after {
            content: url(../img/svg/button-prev.svg);
            @media (max-width: 1450px)  {
                content: url(../img/svg/button-prev--mob.svg);   
            }
        }
  
    }
    .swiper-button-next {
        &::after {
            content: url(../img/svg/button-next.svg);
            @media (max-width: 1450px) {
                content: url(../img/svg/button-next--mob.svg);   
            }
        }
  
    }
}
.btn {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    color: $fonBlack;
    text-align: center;
    font-size: 18px;
    font-style: normal;
    font-weight: 550;
    line-height: normal;
    text-decoration: none;
    cursor: pointer;
    width: 258px;
    height: 258px;
    border: 1px solid rgba(0, 0, 0, 0.2);
    transition: all 0.3s;
    border-radius: 50%;
    &:hover {
        transition: all 0.3s;
        background: $blue;
        border: 1px solid $blue;
        color: #fff;

    }
    @include max(md) {
        font-size: 16px;
    }
    &--small {
        position: relative;
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 16px 24px;
        border-radius: 7px;
        background: #2A2A2A;
        color: #FFF;
        text-align: center;
        font-size: 16px;
        font-style: normal;
        font-weight: 500;
        line-height: normal; 
        transition: all 0.3s;
        cursor: pointer;
        width: fit-content;
        &:hover {
            transition: all 0.3s; 
            background: $grey;
            color: #2A2A2A;
        }
    }

}

