            padding-top: 351px;
            padding-left: 22px;
            align-items: flex-end;
            @include max(sm) {
                padding-top: 0;
                padding-left: 0;
            }       
        }
        
    }
    .case {
        position: relative;
        display: flex;
        justify-content: flex-start;
        align-items: flex-start;
        flex-flow: column nowrap;
        width: 100%;
        margin-bottom: 326px;
        max-width: 477px;
        @include max(sm) {
            margin-bottom: 80px;
        }  
        &__big-margin {
            margin-bottom: 373px;
            max-width: 660px;
            @include max(sm) {
                margin-bottom: 80px;
            }  
        }
        &__picture {
            position: relative;
            width: 100%;
            display: flex;
            justify-content: center;
            align-items: center;
            border-radius: 42px;
            overflow: hidden;
            &:before {
                position: relative;
                content: '';
                width: 100%;
                padding-top: 65.30%;
            }

            &--1 {
                &:before {
                    padding-top: 65.30%;
                }  
            }
            &--2 {
                &:before {
                    padding-top: 147.82%;
                }  
            }
            img, picture {
                position: absolute;
                top: 0;
                left: 0;
                right: 0;
                bottom: 0;
                width: 100%;
                height: 100%;
                object-fit: cover;
                object-position: center center;
            }
        }
        &__labels {
            position: absolute;
            top: 30px;
            right: 30px;
            display: flex;
            flex-flow: column nowrap;
            justify-content: flex-start;
            align-items: flex-end;
        }
        &__label {
            position: relative;
            width: fit-content;
            justify-content: center;
            align-items: center;
            padding: 12px 18px;
            border-radius: 33px;
            border: 1px solid #D8D8D8;
            background: #F4F4F4;
            color: $fonBlack;
            font-size: 14px;
            font-style: normal;
            font-weight: 500;
            line-height: normal;
            cursor: pointer;
            text-decoration: none;
            transition: all 0.3s;
            margin-bottom: 20px;
            &:hover {
                transition: all 0.3s;
                background: $blue;
                border: 1px solid $blue;
                color: #fff;
            }
            &:last-child{
                margin-bottom: 0;
            }
        }
        &__desc {
            position: relative;
            display: flex;
            justify-content: space-between;
            align-items: flex-start;
            width: 100%;
            margin-top: 40px;
            @include max(mdHeader) {
                flex-flow: column nowrap;
            }

            &__title {
                color: $fonBlack;
                font-size: 35px;
                font-style: normal;
                font-weight: 500;
                line-height: 92.6%; /* 32.41px */
                letter-spacing: -1.225px; 
                max-width: 382px;
                @include max(mdHeader) {
                    margin-bottom: 30px;
                 }
            }
            &__text {
                max-width: 265px;
            }
        }
    }
    
}
